<template lang="pug">
.cp-about
  .banner
    p.p1 关爱无处不在
    p.p2 CARE YOU CAN TOUCH
    p.p3 让机器人进入千家万户，成为服务机器人行业的领先企业
  #company-profile.w
    h2 关于我们
    h3 About Us
    h4 智能服务机器人研发及产业化领先企业
    p 弗徕威智能机器人科技（上海）有限公司，成立于2012年，注册于上海浦东，在北京、深圳、苏州、成都、圣何塞等地分别设有研究中心和营销中心。企业前身为哈工大、北航共同成立的研究机构，现已发展成为一家集服务机器人研发、生产及产品销售为一体的高科技企业。目前，公司拥有产业链完整的自有技术体系，数亿元高研发投入，拥有完整的自有技术体系，全国服务机器人已授权发明专利排名前三。
    p 弗徕威是国内率先参与制订服务机器人企业标准，并获得首批认证。弗徕威是上海市重大技术装备独立研制责任单位、上海市人工智能创新发展专项支持单位、住建部《居住区机器人服务管理技术规程》制定单位、国家部委《养老服务课题》技术研究单位。企业整合全球研发资源，推动机器人技术与人工智能、移动互联、云计算和物联网的融合和创新，引领智慧家庭、智慧社区以及智慧城市建设。
    img.img(v-if="isMobile", src="../assets/mobile/about-citys.jpg")
    img.img(v-else, src="../assets/web/about-citys.jpg")
  #application-area.w
